import React from "react";
import Link from "gatsby-link";

const ExpoPost = ({ screenshot, title, date, paragraph, github, expoQR }) => (
  <div>
    <row
      style={{
        maxWidth: "100%"
      }}
    >
      <ul
        style={{
          width: 275
        }}
      >
        <img
          src={screenshot}
          alt="ScreenShot"
          style={{
            paddingTop: 55,
            margin: 0,
            maxWidth: "100%",
            maxHeight: "65%"
          }}
        />
        <div
          style={{
            borderRadius: "4px",
            width: 250,
            height: "auto",
            maxWidth: "100%",
            paddingTop: 0,
            margin: 10,
            alignSelf: "center",
            boxShadow: "0 2px 5px rgba(0, 0, 0, 0.2)"
          }}
        >
          <img
            src={expoQR}
            alt="Expo QR"
            style={{
              margin: 0,
              maxWidth: "100%",
              maxHeight: 300,
              alignSelf: "center"
            }}
          />
          <a
            href="https://expo.io/tools#client"
            style={{
              paddingLeft: 5,
              paddingTop: 10,
              fontSize: "0.85rem"
            }}
          >
            Scan with Expo Client
          </a>
        </div>
      </ul>
      <div
        style={{
          margin: 9,
          paddingLeft: 10,
          width: 600,
          maxWidth: "100%"
        }}
      >
        <h3>{title}</h3>
        <h6 style={{ color: "gray", fontWeight: 100 }}>{date}</h6>
          <div dangerouslySetInnerHTML={{__html: paragraph}}/>
      </div>
    </row>
    <row>
      <a
        target="_blank"
        href={github}
        style={{
          fontSize: "0.85rem",
          paddingBottom: 30
        }}
      >
        GitHub
      </a>
    </row>
  </div>
);

export default ExpoPost;



