import React from "react";
import Link from "gatsby-link";

const ScreenshotPost = ({ screenshot, title, date, paragraph, github }) => (
  <div>
    <row
      style={{
        maxWidth: "100%"
      }}
    >
      <img
        src={screenshot}
        alt="ScreenShot"
        style={{
          margin: 0,
          maxWidth: "100%",
          maxHeight: 600
        }}
      />
      <div>
        <h3>{title}</h3>
        <h6 style={{ color: "gray", fontWeight: 100 }}>{date}</h6>
        <blockquote>
          <div dangerouslySetInnerHTML={{__html: paragraph}}/>
        </blockquote>
      </div>
    </row>
    <row>
      <a
        target="_blank"
        href={github}
        style={{
          paddingBottom: 30,
          fontSize: "0.85rem"
        }}
      >
        GitHub
      </a>
    </row>
  </div>
);

export default ScreenshotPost;



